<template>
  <v-card height="100%">
    <v-card-title>
      <!--  or    -->
      {{data.date}}
      {{data.is}}
    </v-card-title>
    <!--      or   v-for  -->
    <v-card-text> 
      <v-container
      id="scroll-target"
      style="height: 300px"
      class="scroll-y"
      >
      <AccountComp  id='ac' v-bind:data="{category:'', name:'', price:'', rate:''}"></AccountComp>
      <span v-for="account in this.data.accounts">
        <AccountComp v-bind:data="{category:account.category, name:account.name, price:account.price, rate:account.rate}"></AccountComp>
      </span>
      </v-container>
    </v-card-text>
    <v-card-actions>
      <v-layout row justify-center>
        <v-flex xs10>
          <v-btn slot="activator" color="orange" dark v-on:click="addbtn" id="add_b"></v-btn>
          <v-dialog v-model="dialog"  persistent max-width="600px" v-if="dialog==true">
            <v-card>
              <v-form ref="form">
                <v-card-title>
                  <span class="headline"> </span>
                </v-card-title>
                <v-card-text>
                  <v-container grid-list-md>
                    <v-layout wrap>
                      <v-flex xs12 class="addData">
                        : {{data.date}}
                        <!-- <v-text-field label="Legal first name*" required></v-text-field> -->
                      </v-flex>
                      <v-flex xs12 class="addData">
                        <br>
                        : {{data.is}}
                      </v-flex>
                      <v-flex xs12>
                        <v-text-field label="" required v-model="addPrice" :rules="[v => !!v || '  ']"></v-text-field>
                      </v-flex>
                      <v-flex xs12>
                        <v-text-field label="" required v-model="addName" :rules="[v => !!v || '  ']"></v-text-field>
                      </v-flex> 
                      <v-flex xs12>
                        <span v-if="data.is==''">
                        <v-select
                          :items="['', '', '', '', '', '']"
                          label=""
                          required
                          v-model="addCategory"
                          :rules="[v => !!v || '  ']"
                        ></v-select>
                        </span>
                        <span v-else>
                          <v-select
                          :items="['', '/', '', '/', '/', '', '/', '', '', '/', '', '']"
                          label=""
                          required
                          v-model="addCategory"
                          :rules="[v => !!v || '  ']"
                          ></v-select>
                        </span>
                      </v-flex>
                      <v-flex xs12>
                        <v-rating v-model="addRate" :rules="[v => !!v || ' ']"></v-rating>
                      </v-flex>
                    </v-layout>
                  </v-container>
                  <small>*indicates required field</small>
                </v-card-text>
                <v-card-actions>
                  <v-btn color="blue darken-1" flat @click="dialog = false">Close</v-btn>
                  <v-btn color="blue darken-1" flat  v-on:click="add">Save</v-btn>
                </v-card-actions>
              </v-form>
            </v-card>
          </v-dialog>
        </v-flex>
      </v-layout>
    </v-card-actions>
  </v-card>
</template>

<script>
import AccountComp from "../components/AccountComp";


  export default{
    name : 'AccountShow',
    props:["data"],
    methods:{
      add:async function(){
        if(this.$refs.form.validate()){
          this.$http.post('/account/create', {
            id: this.$session.get('id'),
            year: parseInt(this.data.date.slice(0,4)),
            month: parseInt(this.data.date.slice(5,7)),
            day: parseInt(this.data.date.slice(8,10)),
            is: this.data.is,
            price: this.addPrice,
            name:this.addName,
            category: this.addCategory,
            rate: this.addRate
            })
            .then((result)=>{
              console.log(result)
              this.dialog=false
              alert(this.dialog)
              this.$refs.form.reset()
            })
            .catch((err)=>{
              console.log(err)
            })
        }
      },
      reset(){
        // return this.$refs.form.reset()
      },
      addbtn:function(){
        if(this.data.date==null){
          alert('  ');
          this.dialog=false;
        }
        else this.dialog=true;
      },
    },
    data:function(){
      return{
        addRate:null,
        addName:null,
        addCategory:null,
        addPrice:null,
        dialog: false,
        accounts:this.data.accounts
      }
    },
    components:{
      AccountComp
    }
  }
</script>

<style>
.addData{
  font-size: 18px
}
#add_b{
  align-content: center
}
#ic{
  color:darkgrey
}
</style>



